import React, { Component } from 'react';

// Assets
import '../../assets/css/singleseries.min.css';
import '../../assets/css/peopledetail.min.css';
import imgfallbacksrc from '../../assets/img/img-fallback-portrait.jpg';

// Components
import FeatMovies from '../../components/featmovies/FeatMovies';
import CastCredits from '../../components/castcredits/CastCredits';
import Loader from '../../components/loader/Loader';

const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class PeopleDetail extends Component {
  state = {
    person: null,
    shows: null
  };

  getData() {
    Promise.all([
      fetch(`https://api.tvmaze.com/people/${this.props.match.params.id}?embed=castcredits`),
      fetch(`https://api.tvmaze.com/people/${this.props.match.params.id}/castcredits?embed=show`)
    ])
    .then(([person, shows]) => Promise.all([person.json(), shows.json()]))
    .then(([person, shows]) => {
      this.setState({ person, shows });
      document.title = person.name + ' - React Cinema';
    });
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    this.getData();
  }

  componentDidUpdate(prevProps) {
    prevProps.match.params !== this.props.match.params &&
    this.getData();
  }

  calcAge(dateString) {
    var birthday = +new Date(dateString);
    return ~~ ((Date.now() - birthday) / (31557600000));
  }

  render() {
    const { person, shows } = this.state;
    const age = person ? person.birthday && this.calcAge(person.birthday) : '';
    let castsID = person ? person._embedded.castcredits.map(item => Number(item._links.character.href.substring(33))) : null;

    return (
      <div className="detail-wrapper">
        {
          !person && <Loader />
        }
        <div className="content-detail">
          {
            person
            &&
            <div>
              {
                person.image
                &&
                <div className="id-wrapper">
                  <div className="img-detail person-img-detail">
                    <img alt={person.name} src={person.image ? insertStr(person.image.medium, 4, 's') : imgfallbacksrc} className="image-show" />
                  </div>
                </div>
              }
              <div className="info-detail people-detail">
                <h1 className="title-detail">{person.name}</h1>
                <p><b>Gender:</b> {person.gender ? person.gender : '-'}</p>
                <p><b>Age:</b> {age ? age : '-'}</p>
                <p><b>Birthday:</b> {person.birthday ? person.birthday : '-'}</p>
                <p><b>Country:</b> {person.country ? person.country.name : '-'}</p>
              </div>
            </div>
          }
          <CastCredits castsID={castsID} shows={shows} person={person} />
        </div>
        <FeatMovies shows={shows} />
      </div>
    )
  }
}

export default PeopleDetail;



