import React from 'react';
import { Link } from 'react-router-dom';

// Assets
import imgfallbacksrc from '../../assets/img/img-fallback-portrait.jpg';
import removeDiacritics from '../removeDiacritics';

const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

const FeatMovies = props => (
  <div className="sidebar-detail">
    <div className="sd-content">
    {
      props.shows
      &&
      <div className="sd-content-wrapper">
        <h6 className="content-title">Appeared On</h6>
        <div className="cast-list">
          {
          props.shows.map((item, i) => {
            let str = item._embedded.show.name;
            str = removeDiacritics(str);
            str = str.replace(/\W+/g, '-').toLowerCase();
            return (
            <div key={i} className="cast-box">
              <Link to={`/shows/${item._embedded.show.id}/${str}`} className="img-cast">
                <img src={item._embedded.show.image ? insertStr(item._embedded.show.image.medium, 4, 's') : imgfallbacksrc} alt={item._embedded.show.name} />
              </Link>
              <div className="info-cast">
                <Link to={`/shows/${item._embedded.show.id}/${str}`} className="person-name">{item._embedded.show.name}</Link>
                <p>Rating: {item._embedded.show.rating.average ? item._embedded.show.rating.average : '-'}</p>
                <p>{item._embedded.show.network && item._embedded.show.network.name}</p>
              </div>
            </div>
            );
          })
          }
        </div>
      </div>
    }
    </div>
  </div>
);

export default FeatMovies;



