import React from 'react';
import { Link } from 'react-router-dom';

// Assets
import imgfallbacksrc from '../../assets/img/img-fallback-landscape.jpg';

const addImgDefaultSrc = ev => ev.target.src = imgfallbacksrc;
const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

const PrevEpsItem = ({ episode, idShow, showName }) => (
  <div key={episode.id} className="episode-box">
    <Link to={`/shows/${idShow}/${showName}/${episode.url.substring(22)}`} className="img-episode-wrapper">
      <img src={episode.image && episode.image.medium ? insertStr(episode.image.medium, 4, 's') : imgfallbacksrc} alt={episode.name} onError={addImgDefaultSrc} />
    </Link>
    <div className="episode-info">
      <div className="info-season">S{episode.season} - Ep: {episode.number}</div><br/>
      <Link to={`/shows/${idShow}/${showName}/${episode.url.substring(22)}`} className="episode-title">{episode.name}</Link>
      <p>Airdate: {episode.airdate}</p>
    </div>
  </div>
);

const PrevEps = props => (
  <div className="previous-episodes">
    <h6 className="content-title">Previous Episodes</h6>
    {
      props.totalEpisodes > 0 &&
      <div className="episodes-wrapper">
        {
          props.previousEpisodes.slice(-6).reverse().map(episode => (
            <PrevEpsItem key={episode.id} episode={episode} idShow={props.idShow} showName={props.showName} />
          ))
        }
      </div>
    }
    {
      props.totalEpisodes > 6 &&
      <div className="button-wrapper">
        <Link to={`/shows/${props.idShow}/${props.showName}/episodes`} className="button">VIEW FULL EPISODES</Link>
      </div>
    }
  </div>
);

export default PrevEps;



