import React from 'react';

// Assets
import loadersrc from '../../assets/img/loader.svg';

const Loader = () => (
  <div className="loader-wrapper">
    <div className="loader">
      <img
        alt="Loader Icon"
        src={loadersrc}
      />
    </div>
  </div>
);

export default Loader;



