import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import '../../assets/css/shows.min.css';

// Components
import Loader from '../../components/loader/Loader';
import Pagination from '../../components/pagination/Pagination';
import removeDiacritics from '../../components/removeDiacritics';

const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class Shows extends Component {
  state = {
    allSeries: [],
    pageOfItems: [],
    genres: [],
    indexPage: Number(this.props.location.search.substring(6)) || 1
  };

  onChangePage(pageOfItems) {
    window.scrollTo(0, 0);
    this.setState({ pageOfItems: pageOfItems });
  }

  componentDidMount() {
    document.title = 'TV Series Library - React Cinema';
    fetch('https://api.tvmaze.com/shows')
    .then(res => res.json())
    .then(allSeries => {
      let collection = [];
      let allGenres = allSeries.map(series => series.genres);
      allGenres.map(item => item.map(itemchild => collection.push(itemchild)));
      let genres = collection.filter((value, index, self) => self.indexOf(value) === index);
      this.setState({ allSeries, genres });
    });
  }

  componentDidUpdate() {
    let newIndex = Number(this.props.location.search.substring(6));
    this.state.indexPage !== newIndex && this.setState({ indexPage: newIndex })
  }

  render() {
    const { allSeries, pageOfItems, indexPage } = this.state;

    return (
      <React.Fragment>
        <h1 className="title-page">SHOWS</h1>
        <div className="shows-list-wrapper">
          <ul className="shows-list">
            {
              allSeries.length === 0 && <Loader />
            }
            {
              pageOfItems.length !== 0
              &&
              pageOfItems.map(series => {
                let str = series.name;
                str = removeDiacritics(str);
                str = str.replace(/\W+/g, '-').toLowerCase();
                return (
                  <li key={series.id}>
                    <Link to={`/shows/${series.id}/${str}`}>
                      <img src={insertStr(series.image.medium, 4, 's')} alt={series.name} />
                      <div className="title-rating">
                        <div className="tr-wrapper">
                          <p>{series.name}</p>
                        </div>
                      </div>
                      <span>Rating: <b>{series.rating && series.rating.average}</b></span>
                    </Link>
                  </li>
                );
              })
            }
          </ul>
          <Pagination items={allSeries} indexPage={indexPage} onChangePage={this.onChangePage.bind(this)} />
        </div>
      </React.Fragment>
    )   
  }
}

export default Shows;



