import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import NavLink from "./NavLink";

// Assets
import '../../assets/css/header.min.css';
import searchIcon from '../../assets/img/search-icon.png';

// Components
import SeriesList from '../serieslist/SeriesList';

class Header extends Component {
  state = {
    series: [],
    seriesName: '',
    isFetching: false
  };

  onSeriesInputChange = e => {
    this.setState({ seriesName: e.target.value, isFetching: true });
    fetch(`https://api.tvmaze.com/search/shows?q=${e.target.value}`)
    .then(res => res.json())
    .then(series => this.setState({ series, isFetching: false }));
  };

  closeSearch = () => {
    this.setState({ seriesName: '' });
  };

  render() {
    const { series, seriesName, isFetching } = this.state;

    return (
      <header className="header">
        <div className="header-wrapper">
          <Link to="/shows" className="logo">REACT<b>CINEMA</b></Link>
          <div className="quick-search">
            <img src={searchIcon} alt="" />
            <input type="text" value={seriesName} onChange={this.onSeriesInputChange} placeholder="Quick search" />
            {
              !isFetching && series.length === 0 && seriesName.trim() !== ''
              &&
              <p>No TV Series have been found with this name</p>
            }
            {
              !isFetching && seriesName.trim() !== '' && <SeriesList list={series} onCloseSearch={this.closeSearch} />
            }
          </div>
          <nav>
            <ul>
              <li>
                <NavLink to="/shows">SHOWS</NavLink>
              </li>
              <li>
                <NavLink to="/schedule">SCHEDULE</NavLink>
              </li>
            </ul>
          </nav>
        </div>
      </header>
    );
  }
}

export default Header;



