import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import '../../assets/css/episodes.min.css';
import imgportraitsrc from '../../assets/img/img-fallback-portrait.jpg';
import imglandscapesrc from '../../assets/img/img-fallback-landscape.jpg';

// Components
import Loader from '../../components/loader/Loader';

const addImgPortraitSrc = ev => ev.target.src = imgportraitsrc;
const addImgLandscapeSrc = ev => ev.target.src = imglandscapesrc;
const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class Episodes extends Component {
  constructor(props) {
    super(props);
    this.state = {
      episodes: null,
      seasons: null
    };
  }

  getEpisodes() {
    Promise.all([
      fetch(`https://api.tvmaze.com/shows/${this.props.match.params.id}/episodes?specials=${this.props.match.params.id}`),
      fetch(`https://api.tvmaze.com/shows/${this.props.match.params.id}/seasons`)
    ])
    .then(([episodes, seasons]) => Promise.all([episodes.json(), seasons.json()]))
    .then(([episodes, seasons]) => {
      this.setState({ episodes, seasons });
      document.title = 'Full Episodes - React Cinema';
    });
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    this.getEpisodes();
  }

  render() {
    const { episodes, seasons } = this.state;

    return (
      <React.Fragment>
        <h1 className="title-page">EPISODES LIST</h1>
        <div className="shows-list-wrapper">
          <div className="episodes-list-wrapper">
            <div className="back-wrapper">
              <Link to={`/shows/${this.props.match.params.id}/${this.props.match.params.showname}`} className="back-to-show">
                <span className="arrow left"></span>
                Back to Show
              </Link>
            </div>
            {
              !seasons && <Loader />
            }
            {
              seasons &&
              seasons.slice().reverse().map(season => (
                season && season.premiereDate ? <Season key={season.id} idShow={this.props.match.params.id} showName={this.props.match.params.showname} episodes={episodes} season={season} /> : false
              ))
            }
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const Season = ({ idShow, showName, episodes, season }) => (
  <div className="season-box">
    <div className="content-detail">
        <div className="id-wrapper">
          <img src={season.image ? insertStr(season.image.medium, 4, 's') || insertStr(season.image.original, 4, 's') : imgportraitsrc} alt={season.name} onError={addImgPortraitSrc} className="img-season" />
        </div>
        <div className="info-detail">
          <h5 className="title-detail">Season {season.number}</h5>
          <p><b>Total Episodes:</b> {season.episodeOrder}</p>
          <p><b>Premiered:</b> {season.premiereDate}</p>
          <p><b>End:</b> {season.endDate}</p>
          <p><b>Network:</b> {season.network ? season.network.name : '-'}</p>
          <p className="title-summary content-title"><b>Summary</b></p>
          <div className="summary" dangerouslySetInnerHTML={{__html: season.summary || 'Summary for this season is not available at the moment.'}}></div>
        </div>
        <div className="wrapper-season-episode">
          <h6 className="content-title">All Episodes</h6>
          <div className="episodes-wrapper">
            {
              episodes.slice().reverse().map(episode => (
                season.number === episode.season &&
                <div key={episode.id} className="episode-box">
                <Link to={`/shows/${idShow}/${showName}/${episode.url.substring(22)}`} className="img-episode-wrapper">
                  <img src={episode.image ? insertStr(episode.image.medium, 4, 's') || insertStr(episode.image.original, 4, 's') : imglandscapesrc} alt={episode.name} onError={addImgLandscapeSrc} />
                </Link>
                <div className="episode-info">
                  {
                    episode.number ?
                    <div className="info-season">S{episode.season} - Ep: {episode.number}</div>
                    :
                    <div className="info-season">SPECIAL EPISODE</div>
                  }
                  <br/>
                  <Link to={`/shows/${idShow}/${showName}/${episode.url.substring(22)}`} className="episode-title">{episode.name}</Link>
                  <p>Airdate: {episode.airdate}</p>
                  <p>Runtime: {episode.runtime ? episode.runtime + ' minutes' : '-'}</p>
                </div>
                </div>
              ))
            }
          </div>
        </div>
    </div>
  </div>
);

export default Episodes;



