import React from 'react';
import { Link } from 'react-router-dom';

// Components
import removeDiacritics from '../removeDiacritics';

const SeriesListItem = ({ series, str, onCloseSearch }) => (
  <li>
    <Link to={`/shows/${series.show.id}/${str}`} onClick={onCloseSearch}>
      {series.show.name}
    </Link>
  </li>
);

const SeriesList = props => (
  <ul className="series-list">
    {
      props.list.map(series => {
        let str = series.show.name;
        str = removeDiacritics(str);
        str = str.replace(/\W+/g, '-').toLowerCase();
        return (
          <SeriesListItem key={series.show.id} series={series} str={str} onCloseSearch={props.onCloseSearch} />
        );
      })
    }
  </ul>
);

export default SeriesList;



