import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import '../../assets/css/schedule.min.css';

// Components
import Loader from '../../components/loader/Loader';
import removeDiacritics from '../../components/removeDiacritics';

class People extends Component {
  constructor(props) {
    super(props);
    this.state = {
      today: null
    };
  }
  
  componentDidMount() {
    window.scrollTo(0, 0);
    fetch('https://api.tvmaze.com/schedule')
    .then(res => res.json())
    .then(res => {
      this.setState({ today: res });
      document.title = 'Today\'s Schedule - React Cinema';
    });
  }
    
  render() {
    const { today } = this.state;
    let lastAirtime = '';

    return (
      <React.Fragment>
        <h1 className="title-page">SCHEDULE</h1>
        <div className="shows-list-wrapper">
          {
            today &&
            <div className="date-today">{today && today[0].airdate}</div>
          }
          <ul className="shows-list schedule-list">
            {
              !today && <Loader />
            }
            {
              today &&
              today.map(list => {
                let str = list.show.name;
                str = removeDiacritics(str);
                str = str.replace(/\W+/g, '-').toLowerCase();
                return (
                  <div key={list.id} className="schedule-box">
                  {
                      lastAirtime !== list.airtime ?
                      <div className="airtime">{lastAirtime = list.airtime}</div>
                      :
                      <div className="airtime empty"></div>
                  }
                    <div className="schedule-info">
                      <Link to={`/shows/${list.show.id}/${str}`} className="showname">{list.show.name}</Link>
                      <div className="type-show">{list.show.type} <i>{list.show.network ? 'on ' + list.show.network.name : ''}</i></div>
                      <div className="duration">{list.show.runtime + ' Min'}</div>
                    </div>
                  </div>
                );
              })
            }
          </ul>
        </div>
      </React.Fragment>
    )
  }
}

export default People;



