import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import imgfallbacksrc from '../../assets/img/img-fallback-portrait.jpg';
import removeDiacritics from '../removeDiacritics';

// Components
import Loader from '../loader/Loader';

const addImgDefaultSrc = ev => ev.target.src = imgfallbacksrc;
const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class CastCredits extends Component {
  state = {
    shows: this.props.shows,
    casts: null
  };
  
  getCasts() {
    Promise.all(this.props.castsID.map(id => fetch(`https://api.tvmaze.com/characters/${id}`)))
    .then(res => Promise.all(res.map(item => item.json()))
    .then(res => this.setState({
      shows: this.props.shows,
      casts: res
    })));
  }

  componentDidUpdate(prevProps) {
    prevProps.shows !== this.props.shows &&
    this.getCasts();
  }

  render() {
    const { shows, casts } = this.state;
    
    return (
      <div className="previous-episodes cast-credits">
        {
          this.props.person && <h6 className="content-title">Cast Credits</h6>
        }
        {
          this.props.person && !casts && <Loader />
        }
        {
          casts
          &&
          <div className="show-wrapper">
            <div className="episodes-wrapper">
              {
                casts &&
                casts.map((episode, i) => {
                  if (episode.id) {
                    let str = shows[i]._embedded.show.name;
                    str = removeDiacritics(str);
                    str = str.replace(/\W+/g, '-').toLowerCase();
                    return (
                      <div key={episode.id} className="episode-box cast-box">
                        <Link to={`/shows/${shows[i]._embedded.show.id}/${str}`} className="img-episode-wrapper">
                          <img src={episode.image && episode.image.medium ? insertStr(episode.image.medium, 4, 's') : imgfallbacksrc} alt={episode.name} onError={addImgDefaultSrc} />
                        </Link>
                        <div className="episode-info">
                          <Link to={`/shows/${shows[i]._embedded.show.id}/${str}`} className="series-title">
                            {shows[i]._embedded.show.name}
                            <span>
                              {
                                shows[i]._embedded.show.premiered ? ' (' + shows[i]._embedded.show.premiered.substring(4, 0) + ')' : ''
                              }
                            </span>
                          </Link>
                          <p>Starring as:</p>
                          <p><b>{episode.name}</b></p>
                        </div>
                      </div>
                    );
                  }
                })
              }
            </div>
          </div>
        }
      </div>
    )
  }
}

export default CastCredits;



