import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import imgfallbacksrc from '../../assets/img/img-fallback-portrait.jpg';

// Components
import removeDiacritics from '../removeDiacritics';

const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class Cast extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cast: '',
      idShow: this.props.idShow
    };
  }

  componentDidUpdate(prevProps) {
    prevProps.idShow !== this.props.idShow &&
    fetch(`https://api.tvmaze.com/shows/${this.props.idShow}/cast`)
    .then(res => res.json())
    .then(cast => this.setState({
      cast,
      idShow: this.props.idShow
    }));
  }

  render() {
    const { cast } = this.state;

    return (
      <div className="sidebar-detail">
        <div className="sd-content">
          {
            cast !== ''
            &&
            <div className="sd-content-wrapper">
              <h6 className="content-title">Cast</h6>
              <div className="cast-list">
                {
                  cast.map((item, i) => {
                    let str = item.person.name;
                    str = removeDiacritics(str);
                    str = str.replace(/\W+/g, '-').toLowerCase();
                    return (
                      <div key={i} className="cast-box">
                        <Link to={`/people/${item.person.id}/${str}`} className="img-cast">
                          <img src={item.person.image ? insertStr(item.person.image.medium, 4, 's') : imgfallbacksrc} alt={item.person.name} />
                        </Link>
                        <div className="info-cast">
                          <Link to={`/people/${item.person.id}/${str}`} className="person-name">{item.person.name}</Link>
                          <p>as {item.character.name}</p>
                        </div>
                      </div>
                    );
                  })
                }
              </div>
            </div>
          }
        </div>
      </div>
    )
  }
}

export default Cast;



