import React, { Component } from 'react';

// Assets
import '../../assets/css/singleseries.min.css';
import imgfallbacksrc from '../../assets/img/img-fallback-portrait.jpg';

// Components
import Cast from '../../components/cast/Cast';
import PrevEps from '../../components/preveps/PrevEps';
import Loader from '../../components/loader/Loader';

const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class SingleSeries extends Component {
  state = {
    show: '',
    showPopup: false
  };

  getData() {
    fetch(`https://api.tvmaze.com/shows/${this.props.match.params.id}?embed=episodes`)
    .then(res => res.json())
    .then(show => {
      this.setState({ show, showPopup: false });
      document.body.classList.remove('no-scroll');
      document.title = show.name + ' - React Cinema';
    });
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    this.getData();
  }

  componentDidUpdate(prevProps) {
    prevProps.match.params !== this.props.match.params &&
    this.getData();
  }

  componentWillUnmount() {
    document.body.classList.remove('no-scroll');
  }

  togglePopup() {
    this.setState({
      showPopup: !this.state.showPopup
    });
    document.body.classList.toggle('no-scroll');
  }

  render() {
    const { show, showPopup } = this.state;
    const totalDays = show ? show.schedule.days.length : 0;
    const totalGenres = show ? show.genres.length : 0;
    const totalEpisodes = show ? show._embedded.episodes.length : 0;
    const CurrentDate = new Date();
    const previousEpisodes = totalEpisodes > 0 && show._embedded.episodes.filter(episode => {
      let GivenDate = new Date(episode.airdate);
      return GivenDate <= CurrentDate;
    });
    let imgUrl = insertStr(show.image ? show.image.medium : '', 4, 's');
    let imgUrlOri = insertStr(show.image ? show.image.original : '', 4, 's');

    return (
      <div className="detail-wrapper">
        {
          show === '' && <Loader />
        }
        {
          show !== ''
          &&
          <div className="content-detail">
            {
              <div className="id-wrapper">
                <div className="img-detail" onClick={this.togglePopup.bind(this)}>
                  <img alt={show.name} src={show.image ? imgUrl : imgfallbacksrc} className="image-show" />
                </div>
                <p className="rating"><b>Rating:</b><br/>{show.rating.average ? show.rating.average : '-'}</p>
                {
                  show.officialSite ? <a href={show.officialSite} className="button" target="_blank" rel="noopener">OFFICIAL SITE</a> : ''
                }
              </div>
            }
            <div className="info-detail">
              <h1 className="title-detail">{show.name}</h1>
              <p><b>Genres:</b>
                {
                  show.genres.map((genre, i) => (
                    <span key={i}> {genre}{show.genres.length > 1 && totalGenres !== i + 1 ? ',' : ''}</span>
                  ))
                }
              </p>
              <p><b>Premiered:</b> {show.premiered ? show.premiered : '-'}</p>
              <p><b>Episodes:</b> {show._embedded.episodes.length ? show._embedded.episodes.length : '-'}</p>
              <p><b>Runtime:</b> {show.runtime ? show.runtime + ' minutes' : '-'}</p>
              <p><b>Schedule:</b> 
                {
                  totalDays > 0 ? show.schedule.days.map((day, i) => (
                    <span key={i}> {day}
                      {
                        show.schedule.days.length > 1 && totalDays !== i + 1 ? ',' : ''
                      }
                    </span>
                  )) : '-'
                }
                {
                  show.schedule.time ? ' at ' + show.schedule.time : ''
                }
              </p>
              <p><b>Show Type:</b> {show.type ? show.type : '-'}</p>
              <p><b>Status:</b> {show.status ? show.status : '-'}</p>
              <p><b>Streamed On:</b> {show.webChannel ? show.webChannel.name : '-'}</p>
              <p className="title-summary content-title"><b>Summary</b></p>
              <div className="summary" dangerouslySetInnerHTML={{__html: show.summary || 'Summary for this show is not available at the moment.'}}></div>
            </div>
            <PrevEps totalEpisodes={totalEpisodes} previousEpisodes={previousEpisodes} idShow={show.id} showName={this.props.match.params.showname} />
          </div>
        }
        {
          show !== '' && showPopup && show.image.original &&
          <div className="popup-wrapper">
            <div className="popup-content">
              <button className="close-popup" onClick={this.togglePopup.bind(this)}>CLOSE</button>
              <img alt={show.name} src={show.image ? imgUrlOri : imgfallbacksrc} className="image-show" />
            </div>
          </div>
        }
        <Cast idShow={show.id} />
      </div>
    )
  }
}

export default SingleSeries;



