import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';

// Containers
import SingleEpisode from '../../containers/singleepisode/SingleEpisode';
import Episodes from '../../containers/episodes/Episodes';
import SingleSeries from '../../containers/singleseries/SingleSeries';
import Shows from '../../containers/shows/Shows';
import PeopleDetail from '../../containers/peopledetail/PeopleDetail';
import Schedule from '../../containers/schedule/Schedule';

const Main = () => (
  <Switch>
    <Redirect exact from="/" to="/shows" />
    <Route path="/shows/:showid/:showname/episodes/:id/:showepisodename" component={SingleEpisode} />
    <Route path="/shows/:id/:showname/episodes" component={Episodes} />
    <Route path="/shows/:id/:showname" component={SingleSeries} />
    <Route exact path="/shows" component={Shows} />
    <Route path="/people/:id" component={PeopleDetail} />
    <Route exact path="/schedule" component={Schedule} />
  </Switch>
);

export default Main;



