import React, { Component } from 'react';
import { Link } from 'react-router-dom';

// Assets
import '../../assets/css/singleseries.min.css';
import '../../assets/css/singleepisode.min.css';
import imgfallbacksrc from '../../assets/img/img-fallback-landscape.jpg';

// Components
import Loader from '../../components/loader/Loader';

const insertStr = (str, index, value) => str.substr(0, index) + value + str.substr(index);

class SingleEpisode extends Component {
    constructor(props) {
        super(props);
        this.state = {
            idShow: this.props.match.params.showid,
            showName: this.props.match.params.showname,
            episode: null
        };
    }
    
    getData() {
        fetch(`https://api.tvmaze.com/episodes/${this.props.match.params.id}`)
        .then(res => res.json())
        .then(episode => {
            this.setState({ episode });
            document.title = episode.name + ' - React Cinema';
        });
    }

    componentDidMount() {
        window.scrollTo(0, 0);
        this.getData();
    }

    render() {
        const { idShow, showName, episode } = this.state;

        return (
            <div className="detail-wrapper episode-wrapper">
              {
                !episode && <Loader />
              }
              {
                episode &&
                <div className="content-detail">
                    <div className="id-wrapper">
                        <div className="img-detail">
                            <img alt={episode.name} src={episode.image ? insertStr(episode.image.original, 4, 's') : imgfallbacksrc} className="image-show" />
                            <div className="info-episode">
                                <h1 className="title-detail">{episode.name}</h1>
                            </div>
                        </div>
                    </div>
                    <div className="info-detail">
                        <div className="season-episode">
                            <p>Season {episode.season ? episode.season : '-'}</p>
                            <p>Episode {episode.number ? episode.number : '-'}</p>
                        </div>
                        <p><b>Airdate:</b> {episode.airdate ? episode.airdate : '-'}</p>
                        <p><b>Runtime:</b> {episode.runtime ? episode.runtime + ' minutes' : '-'}</p>
                        <p className="title-summary content-title"><b>Summary</b></p>
                        <div className="summary" dangerouslySetInnerHTML={{__html: episode.summary || 'Summary for this episode is not available at the moment.'}}></div>
                        <div className="button-wrapper">
                            <Link to={`/shows/${idShow}/${showName}/episodes`} className="button color-white">SEE OTHER EPISODES</Link>
                        </div>
                    </div>
                </div>
              }
            </div>
        );
    }
}

export default SingleEpisode;



